/* Conteneur principal */
.messagelist {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 1200px;
  margin: auto;
  font-family: 'Helvetica Neue', sans-serif;
  background-color: #ffffff;
  color: #222;
}

/* Titre principal */
.messagelist h2 {
  font-size: 2.2rem;
  text-align: center;
  margin-bottom: 1rem;
  color: #004080;
}

/* Tous les titres de message sur fond gris clair arrondi */
.messagelist h3 {
  background-color: #f0f0f0;
  padding: .2rem .5rem;
  border-radius: 12px;
  font-size: .8rem;
  color: #333;
}

/* Bloc de message */
.messagelist > div {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: .5rem;
  background-color: #fefefe;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

/* Texte du message */
.messagelist p {
  font-size: 12px;
  line-height: 1.6;
  margin: 0.5rem 0;
}

/* Médias associés */
.messagelist h4 {
  margin-top: 1rem;
  font-size: 1.1rem;
  color: #555;
}

/* Grille des médias */
.messagelist .media-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

/* Images et vidéos */
.messagelist .media-item {
  position: relative;
}

.messagelist img,
.messagelist video {
  width: 100%;
  height: auto;
  border-radius: 6px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1);
}

/* Titre superposé en bas de l'image ou vidéo */
.messagelist .media-item h3 {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(240, 240, 240, 0.85);
  padding: 0.5rem 1rem;
  border-radius: 8px 8px 0 0;
  font-size: 1rem;
  color: #333;
  text-align: center;
  margin: 0;
  width: calc(100% - 2rem);
}

/* Responsive mobile */
@media screen and (max-width: 600px) {
  .messagelist {
    /* padding: 1rem; */
  }

  .messagelist h2 {
    font-size: 1.6rem;
  }

  .messagelist h3 {
    font-size: 1.2rem;
  }

  .messagelist p {
    font-size: 0.95rem;
  }
}

/* Vues conditionnelles */
.message-card.text-only h3 {
  cursor: pointer;
}

.message-card.image-only h3,
.message-card.video-only h3,
.message-card.image-and-video h3 {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(240, 240, 240, 0.85);
  padding: 0.5rem 1rem;
  border-radius: 8px 8px 0 0;
  font-size: 1rem;
  color: #333;
  text-align: center;
  margin: 0;
  width: calc(100% - 2rem);
}
